html {
  font-family: sans-serif;
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:active,
a:hover {
  outline-width: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type=button] [type=reset] [type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/**
 * Set up here the general appearance of the theme.
 */
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1, h2, h3, h4 {
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 700;
  font-style: normal;
}

.flex-grid article {
  opacity: 0;
}

.flex-grid article.shown {
  opacity: 1;
}

.flex-grid article.animate {
  animation: moveUp 0.65s;
  opacity: 1;
}

@keyframes moveUp {
  from {
    transform: translateY(200px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  2%, 18% {
    transform: translate3d(-1px, 0, 0);
  }
  4%, 16% {
    transform: translate3d(2px, 0, 0);
  }
  6%, 10%, 14% {
    transform: translate3d(-4px, 0, 0);
  }
  8%, 12% {
    transform: translate3d(4px, 0, 0);
  }
}
@keyframes zoomIn {
  50% {
    transform: translate(-50%, -50%) scale(1.1);
  }
}
@keyframes pulse {
  0% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.icons-home {
  text-align: center;
}
.icons-home a {
  display: inline-block;
  padding: 0.9375rem;
  margin: 0.125rem;
  border-radius: 50%;
  border: 0.125rem solid #ffffff;
  line-height: 0;
  transition: all 0.7s;
}
.icons-home a .icon {
  fill: #ffffff;
  width: 18px;
  height: 18px;
}
@media (min-width: 37.5rem) {
  .icons-home a .icon {
    width: 30px;
    height: 30px;
  }
}
.icons-home a:hover {
  background: #ffffff;
}
.icons-home a:hover .icon {
  fill: #333333;
}

.down {
  position: absolute;
  bottom: 50px;
  width: 100%;
  display: block;
  text-align: center;
}
.down .icon {
  position: absolute;
  transform-style: preserve-3d;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  fill: #ffffff;
  animation: pulse 1.3s infinite;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body.has-push-menu,
body.has-push-menu aside,
body.has-push-menu .progress-bar {
  transition: all 0.3s ease;
}

body.has-push-menu {
  overflow-x: hidden;
  position: relative;
  left: 0;
}
body.has-push-menu.push-menu-to-right {
  left: 15rem;
}
body.has-push-menu.push-menu-to-right .progress-bar {
  left: 15rem;
}
body.has-push-menu.push-menu-to-right .bar-header {
  left: 15rem;
}

aside.sidebar {
