.hero {
  position: relative;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
}
.hero + #grid {
  margin-top: -5rem;
}
.hero .gradient,
.hero .pixels {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hero .pixels {
  opacity: 0.8;
  background-image: url(../../assets/img/pixels.png);
}
.hero .gradient {
  background-image: linear-gradient(0deg, #141414 5%, rgba(0, 0, 0, 0) 30%);
}
.hero .content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  width: 80%;
  height: 100%;
  padding-left: 10%;
}
@media (min-width: 37.5rem) {
  .hero .content {
    width: 50%;
  }
}
.hero .content .date {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.75);
}
.hero .content .title {
  font-size: 2.5rem;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  margin: 0.625rem 0;
}
@media (min-width: 37.5rem) {
  .hero .content .title {
    font-size: 3.125rem;
  }
}
.hero .content .description {
  font-size: 1.5rem;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  margin: 0;
}
@media (min-width: 37.5rem) {
  .hero .content .description {
    font-size: 1.625rem;
  }
}
.hero .content .buttons {
  margin-top: 3.125rem;
}
.hero .content .button {
  display: inline-block;
  font-size: 1.125rem;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 0.3125rem;
  padding: 0.625rem 0.9375rem;
  transition: all 0.3s;
}
@media (min-width: 37.5rem) {
  .hero .content .button {
    font-size: 1.125rem;
  }
}
.hero .content .button:hover {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 2px 2px 3px 0px rgba(0, 0, 0, 0.2);
}
.hero .content .button svg {
  width: 1.5625rem;
  height: 1.5625rem;
  margin-right: 0.625rem;
  vertical-align: middle;
  color: #ff0a16;
}
.hero .content .button span {
  vertical-align: middle;
}

.search-wrapper {
  position: fixed;
  top: 30px;
  width: 100%;
  padding-right: 5%;
  padding-left: 5%;
  transform: translateY(-200px);
  z-index: 19;
}
.search-wrapper.active {
  transform: translateY(0);
}

.search-form {
  position: relative;
  top: 0;
  width: 100%;
  transform: translateX(-200px);
  transition: all 200ms 100ms cubic-bezier(0, 0.6, 0.4, 1);
  opacity: 0;
  z-index: 19;
}
.search-form .search-field {
  width: 100%;
  height: 2.5rem;
  line-height: 1.875rem;
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 1.25rem;
  color: #141414;
  background-color: #ffffff;
  border: 0;
  border-radius: 1.25rem;
  padding: 0.3125rem 1.5625rem;
  outline: 0;
}
.search-form:focus {
  outline: 0;
}
.search-form.active {
  top: 0;
  transform: translateX(0);
  opacity: 1;
}
.search-form .search-list {
  position: absolute;
  width: 100%;
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 300;
  font-style: normal;
  display: none;
  padding: 0 1.25rem;
  margin: 1.25rem 0;
  list-style-type: none;
}
@media (max-width: 37.4rem) {
  .search-form .search-list {
    height: 380px;
    overflow: scroll;
  }
}
.active .search-form .search-list {
  display: block;
}
.search-form .search-list .entry-date {
  float: right;
  display: none;
  font-size: 0.875rem;
}
@media (min-width: 37.5rem) {
  .search-form .search-list .entry-date {
    display: inline;
  }
}
.search-form .search-list .entry-category {
  text-transform: uppercase;
  background-color: #ff0a16;
  margin-right: 0.3125rem;
  height: 1.25rem;
  border-radius: 0.625rem;
  font-size: 0.75rem;
  padding: 0.125rem 0.5rem;
  color: #ffffff;
}
.search-form .search-list a {
  color: #ffffff;
  text-decoration: none;
  display: block;
  padding: 0.9375rem 0;
  width: 100%;
  border-bottom: 1px solid #333333;
  transition: all 0.3s;
  line-height: 1.5625rem;
}
.search-form .search-list a:hover {
  color: #ff0a16;
}
.search-form .search-list a:hover .entry-category {
  color: #ffffff;
}
.search-form .icon-remove-sign {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 1.625rem;
  height: 1.625rem;
  padding: 0.3125rem;
  fill: #ffffff;
  background-color: #ff0a16;
  border-radius: 13px;
  margin: 0.4375rem;
  cursor: pointer;
  outline: 0;
  z-index: 1;
}

.search-overlay {
  overflow: hidden;
}
.search-overlay:after {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  background-color: #141414;
  content: "";
  z-index: 18;
}

.img-rounded {
  border-radius: 50%;
}

html,
body {
