.modal .mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  animation: 1s fadeIn ease both;
}
.modal .window {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1001;
  transform: translate(-50%, -50%);
  width: calc(100% - 2.5rem);
  max-width: 50rem;
  background-color: #fff;
  border-radius: 0.625rem;
  padding: 0.3125rem 0;
  box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
  animation: 0.3s fadeIn 0.5s ease both;
}
@media (min-width: 37.5rem) {
  .modal .window {
    min-width: 25rem;
  }
}
.modal .window .close {
  position: absolute;
  right: -0.625rem;
  top: -0.625rem;
  width: 1.875rem;
  height: 1.875rem;
  fill: #fff;
  background-color: #141414;
  border-radius: 50%;
  border: 2px solid #fff;
  padding: 0.3125rem;
  cursor: pointer;
}
.modal .window .header {
  text-align: center;
  border-bottom: 1px solid #f2f2f2;
  padding: 0.9375rem;
}
.modal .window .header h2,
.modal .window .header p {
  margin: 0;
  font-size: 0.8125rem;
}
.modal .window .content {
  max-height: 90vh;
  overflow: auto;
  padding: 0.625rem;
}
.modal .window .content ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
@media (min-width: 37.5rem) {
  .modal .window .content ul {
    flex-direction: row;
  }
}
.modal .window .content ul li {
  flex: 1;
  margin: 0.9375rem;
}
.modal .window .content ul li figure {
  position: relative;
  margin: 0;
}
.modal .window .content ul li figure:after {
  content: "Read now";
  text-transform: uppercase;
  display: inline-block;
  background: #ff0a16;
  color: #fff;
  font-size: 0.8125rem;
  font-weight: 700;
  line-height: 1.5rem;
  padding: 0 0.5rem;
  position: absolute;
  bottom: 10%;
  left: -5px;
  transition: background 0.2s;
}
.modal .window .content ul li img {
  display: block;
  max-width: 100%;
  height: auto;
  transition: all 0.2s ease-in-out;
  position: relative;
}
.modal .window .content ul li a {
  font-weight: normal;
  color: #141414;
  text-decoration: none;
  transition: all 0.3s;
}
.modal .window .content ul li a:hover {
  color: rgba(0, 0, 0, 0.5);
}
.modal .window .content ul li a:hover img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}
.modal .window .content ul li a:hover figure:after {
  background: #141414;
}
.modal .window .content ul li h3 {
  font-size: 1.25rem;
  margin-bottom: 0;
}
.modal .window .content ul li p {
  display: none;
}
@media (min-width: 37.5rem) {
  .modal .window .content ul li p {
    display: block;
    font-size: 0.9375rem;
    line-height: 1.25rem;
  }
}
.modal.closed {
  display: none;
}
.modal.exit .window {
  animation: 0.5s fadeIn 0.5s both, 0.5s zoomIn 0.5s both;
}

